@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
html,
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #000000;
  font-family: "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.btn {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  text-decoration: none;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  background-color: #68B738;
  border: 0;
}
.btn:hover {
  background-color: #5eaa2f;
}
.btn:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.6);
}
.page-header {
  display: flex;
  position: relative;
  z-index: 10;
  flex-direction: column;
}
@media (min-width: 768px) {
  .page-header {
    flex-direction: row;
    justify-content: space-between;
    margin: 24px 30px 26px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    width: 1220px;
    margin: 53px auto 30px;
  }
}
.page-header__logo {
  display: block;
  position: relative;
  width: 100%;
  height: 65px;
  border-bottom: 1px solid #ebebeb;
}
.page-header__logo::before {
  content: "";
  display: block;
  width: 34px;
  height: 38px;
  position: absolute;
  left: 20px;
  top: 12px;
  background-image: url("../img/logo-sign-mobile.svg");
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 174px;
    height: 57px;
    border: none;
  }
  .page-header__logo::before {
    display: none;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    width: 202px;
    height: 59px;
  }
}
.page-header__logo img {
  width: 101px;
  height: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -8px);
}
@media (min-width: 768px) {
  .page-header__logo img {
    position: relative;
    top: 0;
    left: 0;
    width: 174px;
    height: 52px;
    transform: none;
  }
}
@media (min-width: 1440px) {
  .page-header__logo img {
    width: 202px;
    height: 59px;
  }
}
.page-header__logo:hover img {
  opacity: 0.6;
}
.page-header__logo:active img {
  opacity: 0.3;
}
.main-nav {
  position: relative;
}
@media (min-width: 768px) {
  .main-nav {
    margin-left: auto;
  }
}
.main-nav__toggle {
  display: block;
  width: 65px;
  height: 65px;
  position: absolute;
  top: -66px;
  right: 0;
  border: none;
  background-color: #ffffff;
  background-image: url("../img/icon_close.svg");
  background-size: 18px 18px;
  background-position: center;
  background-repeat: no-repeat;
}
