}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav__list {
    margin-top: 13px;
    flex-direction: row;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    margin-top: 14px;
    flex-direction: row;
  }
}
.main-nav__list--closed {
  display: none;
}
