  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  display: block;
  box-sizing: border-box;
  min-height: 65px;
  padding: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 25px;
